import axios from 'axios'
import qs from 'qs' // qsinstall  axios  qs  axios 
axios.defaults.withCredentials = true // cookie
axios.defaults.timeout = 5000 //   5000ms

axios.post = function (url, data = {}) {
  return axios({
    method: 'post', // 
    withCredentials: true, // truecookie,cookiefalse
    url: url, // 
    data: qs.stringify(data), // post ,
    timeout: 5000, // , 
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
    }
  })
}

axios.get = function (url, params = {}) {
  return axios({
    method: 'get', // 
    withCredentials: true, // truecookie,cookiefalse
    url, // 
    params, // get 
    timeout: 5000 // , 
  })
}

axios.interceptors.response.use(response => {
  return response.data // BACKjson
}, error => Promise.reject(error.response))

export default axios


